import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StoreComponent } from './public/store/store.component';
import { LoginComponent } from './public/login/login.component';
import { UserdashboardComponent } from './user/userdashboard/userdashboard.component';
import { PubliclayoutComponent } from './public/shared/publiclayout.component';
import { UserlayoutComponent } from './user/shared/userlayout.component';
import { CartComponent } from './public/cart/cart.component';
import { UserAuthGuardService } from './services/user-auth-guard.service';
import { AdminAuthGuardService } from './services/admin-auth-guard.service';
import { UnauthorizeComponent } from './public/unauthorize/unauthorize.component';

const publicRoutes: Routes = [
    { path: '', component: StoreComponent },
    { path: 'cart', component: CartComponent },
    { path: 'login', component: LoginComponent },
    { path: 'unauthorize', component: UnauthorizeComponent }
];

const userRoutes: Routes = [
    { path: '', component: UserdashboardComponent },
];

const routes: Routes = [
    { path: '', component: PubliclayoutComponent, children: publicRoutes },
    { path: 'user', component: UserlayoutComponent, children: userRoutes, canActivate: [UserAuthGuardService] },
    { path: 'admin', loadChildren: 'app/admin/admin.module#AdminModule', canActivate: [AdminAuthGuardService] },
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule],
})
export class AppRoutingModule { }



